import { createStore } from 'vuex'
import axios from "axios"
//ÈáçÊñ∞Êï¥ÁêÜÂæåvuex‰ªçÁÑ∂ÊúÉ‰∏çË¶ãÔºåË≥áË®äÂèØ‰ª•ÈÄèÈÅélocalStorageÁ∑©Â≠ò

export default createStore({
	state: {
		userToken: localStorage.getItem('userToken') || '',

		// Ë≥áÂÆâËßíÂ∫¶ÁöÑË©±Ôºåuser info‰∏çËÉΩÂ≠òÂú®localStorageÔºåÂÉÖËÉΩÂ≠òÂéªË≠òÂà•ÂåñÁöÑtoken
		//Âú®ÈÄôÈÇäÂ∞àÊ°àÊÄïserver‰∏çÂ§†ÂäõÔºåÂ∞±ÂÖàÈÇÑÊòØÂ≠òÂú®localStorage
		userInfo: null
	},
	getters: {
	},
	actions: {
		//dispatchÔºö ÂèñÂÖ∂‰ªñÁöÑactions
		//commitÔºö   ÂèñÂÖ∂‰ªñÁöÑmutations
		checkUserToken ({ dispatch, commit }, token) {

			//ÂÉÖË®ªÂÜäÊàñÁôªÂÖ•ÊàêÂäü
			//Â∞átokenÂ≠òÂÖ•localStorage
			if(token){
				localStorage.setItem('userToken', token)
			}

			//ÈÄèÈÅéPromise‰æÜËôïÁêÜÈùûÂêåÊ≠•Ë°åÁÇ∫
			return new Promise((resolve, reject) => {

				//1.Á¢∫Ë™çÊòØÂê¶ÊúâtokenÔºåÊúâÁöÑË©±Áï∂ÂÅöÂ∑≤Ë®ªÂÜäÁôªÂÖ•ÊàêÂäü 
				if(localStorage.getItem('userToken')) {
					dispatch('checkUserInfo')
					.then((response) => {
						// checkUserInfoË™¨Ë´ãÊ±ÇÊàêÂäü
						resolve(response)
					})
					.catch(error => {
						// checkUserInfoË™¨Ë´ãÊ±ÇÂ§±Êïó
						reject(error)
					})
				}else{
					//Ê≤íÊúâtokenÂ∞áË≥áÊñôÊ∏ÖÁ©∫Ôºå‰∏üÁµ¶mutationÂè´‰ªñÊîπËÆästate
					commit('setUserInfo', null)
					reject('No token')
				}
			})
		},
		checkUserInfo ({ commit }) {
			//ÈÄèÈÅéPromise‰æÜËôïÁêÜÈùûÂêåÊ≠•Ë°åÁÇ∫---Á¢∫Ë™çÊòØÂê¶ÊúâuserInfo
			return new Promise((resolve, reject) => {

				// !Ê≠£ÂºèÂ∑•‰ΩúÂæå‰∏çË¶ÅÈÄôÊ®£ÂÇ≥‰ΩøÁî®ËÄÖË≥áË®ä!
				if(localStorage.getItem('userInfo')) {
					const UserInfoData = localStorage.getItem('userInfo')
					commit('setUserInfo', JSON.parse(UserInfoData))
					// ÂëäÁü•‰ªªÂãôÂÆåÊàêÔºå‰∏üUserInfoData
					resolve(UserInfoData)

				}else{
					// 2. ‰ΩøÁî®tokenÂéªÊèõuserinfoÔºå‰∏ÄËà¨Âú®Âà∞headerË£°‰ΩÜÈÄôÈÇäÂÖàÁî®ÂèÉÊï∏ÁöÑÊñπÂºèÊØîËºÉÂ•Ωdebug
					axios.get('https://reqres.in/api/users/4')
					.then((response) => {
						const UserInfoData = response.data.data
						// 3. ÊàêÂäüÂæåÊääË≥áÊñô‰∏üÁµ¶mutationÔºåÂè´‰ªñÊîπËÆästate
						commit('setUserInfo', UserInfoData)

						// !Ê≠£ÂºèÂ∑•‰ΩúÂæå‰∏çË¶ÅÈÄôÊ®£ÂÇ≥‰ΩøÁî®ËÄÖË≥áË®ä!
						localStorage.setItem('userInfo', JSON.stringify(UserInfoData))

						// ÂëäÁü•‰ªªÂãôÂÆåÊàêÔºå‰∏üUserInfoData
						resolve(UserInfoData)
					})
					.catch(error => {
						// ÂëäÁü•‰ªªÂãôÂ§±ÊïóÔºå‰∏¶ÂõûÂÇ≥ÈåØË™§
						reject(error)
					})
				}
			})
		}
	},
	mutations: {
		setUserInfo(state, payload){
			// 4. ÈÄèÈÅémutationsÊäädataÂ∏∂ÂÖ•state.userInfo
			state.userInfo = payload;
			if(!payload){
				//ÁôªÂá∫
				localStorage.removeItem('userToken')
				localStorage.removeItem('userInfo')
			}
		}
	},
})
