<template>
  <div class="scrollView">
    Ê≤íÊúâÁî®Â§ñÈÉ®ÂáΩÂºèÂ∫´ÁöÑÂØ´Ê≥ï
    <nav>
      <button 
        v-for="item in count" :key="item"
        @click="scrollToBlock(`block${item}`)"
      > {{item}} </button>
    </nav>

    <!-- <div ref="block1">1</div>
    <div ref="block2">2</div>
    <div ref="block3">3</div> -->
    <div
      v-for="item in count" :key="item"
      :ref="`block${item}`"
    >
      {{item}}
    </div>

    <ScrollToTopBtn/>
  </div>
</template>
<script>
import ScrollToTopBtn from '@/components/ScrollTo.vue'
export default {
  components: {
    ScrollToTopBtn
  },
  data(){
    return {
      count: 5
    }
  },
  methods:{
    scrollToBlock(ref){
      // HTMLÁõ¥Êé•ÂØ´div
      // const el = this.$refs[ref]

      // Ë∑ëforËø¥ÂúàÔºåÊúÉÂ§ö‰∏ÄÂÄãÈô£ÂàóÔºåÂèØ‰ª•Áî®console.log(this.$refs[ref])ËßÄÂØü
      const el = this.$refs[ref][0]

      //ÂèñÂæóÂÖÉÁ¥†ÁöÑÂ§ßÂ∞èÔºå‰ª•ÂèäÂÖ∂Áõ∏Â∞çÊñºÂèØË¶ñÁØÑÂúç(viewport) ÁöÑ‰ΩçÁΩÆ
      const offsetTarget = el.getBoundingClientRect()
      window.scrollTo({
          top: (offsetTarget.top + window.pageYOffset),
          behavior: 'smooth'
      })
    },
    scrollToTop(){
      window.scrollTo(0,0)
    }
  },
  mounted(){
    //Ë¶ÅÁî®Âà∞mountedÔºå‰∏çËÉΩÁî®Âú®created‰∏≠ÔºåÂõ†ÁÇ∫DomÂÖÉ‰ª∂ÈÇÑÊ≤íË¢´ÊéõËºâÔºåËÆÄ‰∏çÂà∞window
    this.scrollToTop()
  }
}
</script>
<style lang="scss">
$base-color: rgb(74, 202, 174);
.scrollView{
  position: relative;
  nav{
    position: fixed;
    left: 1rem;
